<template>
  <v-card>
    <v-container
      fluid
      style="height: 95vh"
      grid-list-lg
    >
      <v-layout row wrap>
        <v-flex xs3>
          <v-card color="teal accent-4" class="white--text" height="250px">
            <v-card-title primary-title>
              <div class="headline">总单词数</div>
            </v-card-title>
            <v-card-text class="text-md-center display-3">
              {{ data && data.total }}
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs9>
          <v-card color="blue-grey lighten-3" class="white--text" height="250px">
            <v-container fluid grid-list-lg>
              <v-layout column>
                <v-flex xs12>
                  <div>
                    <div class="headline">出现频率最高的单词</div>
                  </div>
                </v-flex>
                <v-flex xs12>
                  <word-frenquency :data="data"></word-frenquency>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-flex>
        <v-flex xs12>
          <v-card color="blue lighten-3" class="white--text" height="400px">
            <v-container fluid grid-list-lg>
              <v-layout column>
                <v-flex xs12>
                  <div>
                    <div class="headline">词云</div>
                  </div>
                </v-flex>
                <v-flex xs12>
                  <word-cloud :data="data"></word-cloud>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-card>
</template>

<script>
import WordFrenquency from './WordFrenquency/index.vue';
import WordCloud from './WordCloud/index.vue';
import MockData from '../mock/frequency';

export default {
  name: 'home',
  data() {
    return {
      data: {},
      interval: null,
    };
  },
  methods: {
    async fetchData() {
      const response = await fetch('data/view');
      this.data = await response.json();
    },
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  mounted() {
    this.fetchData();
    this.interval = setInterval(this.fetchData, 1000);
  },
  components: {
    WordFrenquency,
    WordCloud,
  },
};
</script>



