import * as echarts from 'echarts/lib/echarts';

import 'echarts/lib/chart/bar';
import 'echarts/lib/component/tooltip';
import 'echarts-wordcloud';

const THEME = 'macarons';

const DEFAULT_OPT = {
  bar: {
    color: ['#FFAB00'],
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow',
      },
    },
    grid: {
      top: '15%',
      left: '3%',
      right: '4%',
      bottom: '10%',
      containLabel: true,
    },
    series: [],
  },
  wordCloud: {
    series: [],
  },
};

class Chart {
  constructor(dom) {
    this.echats = echarts.init(dom);
  }
  setOption(opt, type = 'bar') {
    const option = {
      ...DEFAULT_OPT[type],
      ...opt,
    };
    this.echats.setOption(option, THEME);
  }
}

export default Chart;



