<template>
<div class="word-frequency-chart">
</div>
</template>

<script>
import Chart from '../Chart';

export default {
  title: 'FrequencyChart',
  props: {
    data: Object,
  },
  data() {
    return {
      chart: null,
    };
  },
  methods: {
    initOpt() {
      const data = this.data.detail;
      const opt = this.formateData(data.splice(0, 10));
      this.chart.setOption(opt);
    },
    formateData(data) {
      const x = [];
      const y = [];
      data.forEach((d) => {
        x.push(d.word);
        y.push(d.count);
      });
      return {
        color: ['#3398DB'],
        xAxis: [{
          type: 'category',
          axisTick: {
            alignWithLabel: true,
          },
          data: x,
        }],
        yAxis: [
          {
            type: 'value',
          },
        ],
        series: [{
          name: 'Count',
          type: 'bar',
          barWidth: '60%',
          data: y,
        }],
      };
    },
    initChart() {
      this.chart = new Chart(this.$el);
    },
  },
  mounted() {
    this.initChart();
  },
  watch: {
    data(val) {
      if (val) {
        this.initOpt();
      }
    },
  },
};
</script>

<style>
.word-frequency-chart {
  height: 200px;
  width: 100%;
}
</style>



