<template>
<div class="word-cloud">
</div>
</template>

<script>
import Chart from '../Chart';

export default {
  title: 'WordCloud',
  props: {
    data: Object,
  },
  data() {
    return {
      chart: null,
    };
  },
  methods: {
    initOpt() {
      const opt = this.formateData(this.data.detail);
      this.chart.setOption(opt, 'wordCloud');
    },
    formateData(data) {
      const createRandomItemStyle = () => ({
        normal: {
          color: `rgb(${[
            Math.round(Math.random() * 160),
            Math.round(Math.random() * 160),
            Math.round(Math.random() * 160),
          ].join(',')})`,
        },
      });
      const word = data.map(d => ({
        name: d.word,
        value: d.count,
      }));

      return {
        series: [{
          name: 'Word',
          type: 'wordCloud',
          shape: 'pentagon',
          size: ['80%', '80%'],
          autoSize: {
            enable: true,
            minSize: 14,
          },
          textRotation: [0, 45, 90, -45],
          textPadding: 0,
          autoSize: {
            enable: true,
            minSize: 14,
          },
          textStyle: {
            normal: {
              fontFamily: 'sans-serif',
              color: () => `rgb(${[
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
              ].join(',')})`,
            },
            emphasis: {
              shadowBlur: 10,
              shadowColor: '#333',
            },
          },
          data: word,
        }],
      };
    },
    initChart() {
      this.chart = new Chart(this.$el);
    },
  },
  mounted() {
    this.initChart();
  },
  watch: {
    data(val) {
      if (val) {
        this.initOpt();
      }
    },
  },
};
</script>

<style>
.word-cloud {
  height: 300px;
  width: 100%;
}
</style>



